export interface Member {
  /**
   * member's id
   * @format GUID
   * @readonly
   */
  id?: string;
  /**
   * `true` if member completed email verification, otherwise `false`
   * @readonly
   */
  emailVerified?: boolean;
  /**
   * member's role:
   *
   * `OWNER` - member that belongs to site owner
   * `CONTRIBUTOR` - member that belongs to a contributor in site
   * `MEMBER` - other members that registered to site
   */
  role?: RoleWithLiterals;
  /**
   * email to be used when logging in
   * @format EMAIL
   */
  loginEmail?: string | null;
  /** member's full name */
  memberName?: string | null;
  /** member's first name */
  firstName?: string | null;
  /** member's last name */
  lastName?: string | null;
  /**
   * Deprecated: please use `picture` field instead.
   * URL for member's profile image
   * @format URI
   * @deprecated
   */
  imageUrl?: string | null;
  /** member's nickname (exposed to other members) */
  nickname?: string | null;
  /** `PUBLIC` if other members can view this profile, `PRIVATE` otherwise */
  profilePrivacyStatus?: SiteMemberPrivacyStatusWithLiterals;
  /** url segment for accessing the member's profile */
  slug?: string | null;
  /** member's language */
  language?: string | null;
  /**
   * member's status:
   *
   * `ACTIVE` - when member is approved
   * `APPLICANT` - when pending site owner's approval
   * `BLOCKED` - when member is blocked by the site's owner
   * `INACTIVE` - not used
   */
  status?: SiteMemberStatusWithLiterals;
  /**
   * date the member was created
   * @readonly
   */
  creationDate?: Date | null;
  /**
   * date the member was last updated
   * @readonly
   */
  lastUpdateDate?: Date | null;
  /**
   * date of last login
   * @readonly
   */
  lastLoginDate?: Date | null;
  /**
   * member's email addresses
   * (returned when `include_contact_details` is set to `true`)
   */
  emails?: string[];
  /**
   * member's phone numbers
   * (returned when `include_contact_details` is set to `true`)
   */
  phones?: string[];
  /**
   * member's addresses
   * (returned when `include_contact_details` is set to `true`)
   */
  addresses?: Address[];
  /**
   * labels attached to the member by the owner (in contacts dashboard)
   * (returned when `include_contact_details` is set to `true`)
   */
  labels?: string[];
  /**
   * custom fields set for the member by the owner (in contacts dashboard)
   * (returned when `include_contact_details` is set to `true`)
   */
  customFields?: CustomField[];
  /**
   * member's profile picture
   * supports both media-items and external urls
   */
  picture?: Image;
  /** member's user id */
  userId?: string | null;
  /** Groups the the member is registered to */
  groups?: Group[];
  /**
   * member's contact id
   * @readonly
   */
  contactId?: string | null;
}

export enum Role {
  UNDEFINED_ROLE = 'UNDEFINED_ROLE',
  MEMBER = 'MEMBER',
  OWNER = 'OWNER',
  CONTRIBUTOR = 'CONTRIBUTOR',
}

/** @enumType */
export type RoleWithLiterals =
  | Role
  | 'UNDEFINED_ROLE'
  | 'MEMBER'
  | 'OWNER'
  | 'CONTRIBUTOR';

export enum SiteMemberPrivacyStatus {
  UNDEFINED = 'UNDEFINED',
  PUBLIC = 'PUBLIC',
  PRIVATE = 'PRIVATE',
  COMMUNITY = 'COMMUNITY',
}

/** @enumType */
export type SiteMemberPrivacyStatusWithLiterals =
  | SiteMemberPrivacyStatus
  | 'UNDEFINED'
  | 'PUBLIC'
  | 'PRIVATE'
  | 'COMMUNITY';

export enum SiteMemberStatus {
  UNDEFINED_STATUS = 'UNDEFINED_STATUS',
  APPLICANT = 'APPLICANT',
  ACTIVE = 'ACTIVE',
  INACTIVE = 'INACTIVE',
  BLOCKED = 'BLOCKED',
  OFFLINE_ONLY = 'OFFLINE_ONLY',
}

/** @enumType */
export type SiteMemberStatusWithLiterals =
  | SiteMemberStatus
  | 'UNDEFINED_STATUS'
  | 'APPLICANT'
  | 'ACTIVE'
  | 'INACTIVE'
  | 'BLOCKED'
  | 'OFFLINE_ONLY';

export interface Address {
  /** member's street address */
  street?: string | null;
  /** member's city */
  city?: string | null;
  /** member's region */
  region?: string | null;
  /** member's country */
  country?: string | null;
  /** member's postal code */
  postalCode?: string | null;
}

export interface CustomField extends CustomFieldValueOneOf {
  strValue?: string | null;
  numValue?: number;
  dateValue?: Date | null;
  name?: string;
}

/** @oneof */
export interface CustomFieldValueOneOf {
  strValue?: string | null;
  numValue?: number;
  dateValue?: Date | null;
}

export interface Image {
  /** WixMedia image ID. */
  id?: string;
  /** Image URL. */
  url?: string;
  /**
   * Original image height.
   * @readonly
   */
  height?: number;
  /**
   * Original image width.
   * @readonly
   */
  width?: number;
  /** Image alt text. */
  altText?: string | null;
  /**
   * Image filename.
   * @readonly
   */
  filename?: string | null;
}

export interface FocalPoint {
  /** X-coordinate of the focal point. */
  x?: number;
  /** Y-coordinate of the focal point. */
  y?: number;
  /** crop by height */
  height?: number | null;
  /** crop by width */
  width?: number | null;
}

export interface Group {
  /** @format GUID */
  id?: string;
  name?: string;
  type?: string;
}

export interface ListMembersRequest {
  /** for paging - maximum number of records to retrieve */
  limit?: number;
  /** for paging - how many records to skip */
  offset?: number;
  /** sort order - list of field and direction tuples. e.g. `["first_name:asc", "last_name:desc"]` */
  order?: string[];
  /** partial response request - list of field names to get back in response */
  fields?: string[];
  /** filter members with specific status */
  status?: SiteMemberStatusWithLiterals;
  /** whether to include contact details */
  includeContactDetails?: boolean;
}

export interface ListMembersResponse {
  /** members returned by List request */
  members?: Member[];
  /** pagination information */
  pagination?: PaginationResponse;
}

export interface PaginationResponse {
  /** offset that was applied to the request */
  offset?: number;
  /** limit that was applied to the request */
  limit?: number;
  /** total rows available */
  total?: number;
  /** indication that the total count was too expensive to calculate */
  tooManyToCount?: boolean;
}

export interface SearchRequest {
  /** paging - offset and limit */
  paging?: Paging;
  /** search by field */
  searchBy?: SearchBy;
  /** partial response request - list of field names to get back in response */
  fields?: string[];
  /** filters */
  filterBy?: FilterBy;
  /** ASC or DESC order */
  sort?: SortingWithLiterals;
}

export interface Paging {
  /** Number of items to load. */
  limit?: number | null;
  /** Number of items to skip in the current sort order. */
  offset?: number | null;
}

export interface SearchBy {
  /** nick name filter */
  nickname?: string | null;
}

export interface FilterBy {
  /** filter members with specific status */
  status?: string | null;
  /** group Id filter */
  groupId?: string | null;
  /** privacy status filter */
  privacyStatus?: SiteMemberPrivacyStatusWithLiterals;
  /** roles filter */
  roles?: RoleWithLiterals[];
}

export enum Sorting {
  DESC = 'DESC',
  ASC = 'ASC',
}

/** @enumType */
export type SortingWithLiterals = Sorting | 'DESC' | 'ASC';

export interface SearchResponse {
  /** members returned by List request */
  members?: Member[];
  /** pagination information */
  pagination?: PaginationResponse;
}

